/** @jsx jsx */
import * as React from "react"
import { Box, ThemeProvider, Theme, ThemeUIStyleObject, jsx } from "theme-ui"
import { Global } from "@emotion/react"
import SEO from "./seo"

type LayoutProps = {
  children: React.ReactNode, className?: string, theme: Theme, sx?: ThemeUIStyleObject
}

const Layout = ({children, className = ``, theme, sx = undefined }: LayoutProps) => (
  <>
    <ThemeProvider theme={theme}>
      <Global
        styles={(theme) => ({
          "*": {
            boxSizing: `inherit`,
            "&:before": {
              boxSizing: `inherit`,
            },
            "&:after": {
              boxSizing: `inherit`,
            },
          },
          html: {
            fontSize: `18px`,
            WebkitTextSizeAdjust: `100%`,
          },
          img: {
            borderStyle: `none`,
          },
          pre: {
            fontFamily: `monospace`,
            fontSize: `1em`,
          },
          "[hidden]": {
            display: `none`,
          },
        })}
    />
      <SEO />
      <Box sx={sx}>
        <main className={className}>{children}</main>
      </Box>
    </ThemeProvider>
  </>
)

export default Layout
